<style>
  .login-main{
    padding: 0 .4rem;
    position: relative;
    overflow: hidden;
  }
  .input-suffix{
    margin-top: .8rem;
  }
  .login-main .el-link{
    display: block;
    margin: .5rem 0;
    text-align: left;
  }
</style>
<template>
  <div class="login-main">
    <div class="input-suffix">
      <el-input prefix-icon="el-icon-user" v-model="username" placeholder="请输入用户名"></el-input>
    </div>
    <div class="input-suffix">
      <el-input prefix-icon="el-icon-lock" v-model="password" placeholder="请输入密码"></el-input>
    </div>
    <el-link type="primary" :underline="false" href="#/Customer/Register">
      没有帐号？去注册
    </el-link>
    <div class="input-suffix">
      <el-button type="primary" @click="login()" style="width: 80%;">登录</el-button>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      username: '',
      password: ''
    };
  },
  methods: {
    login () {
      this.$router.push('/Application');
    }
  }
};
</script>



