import Vue from 'vue';
import Router from 'vue-router';
import Customer from '@/view/Customer/Customer';
import Login from '@/view/Customer/Login';
import Register from '@/view/Customer/Register';
import Application from '@/view/Application/Application';
import Usercenter from '@/view/Usercenter/Usercenter';
import Menu from '@/view/Newchapter/Menu';
import Chapter from '@/view/Newchapter/Chapter';
import Novel from '@/view/Newchapter/Novel';
import Role from '@/view/Newchapter/Role';
import Volume from '@/view/Newchapter/Volume';
import Menulist from '@/view/Newchapter/Menulist';
import Management from '@/view/Management/Management';
import Bookslist from '@/view/Management/Bookslist';
import Details from '@/view/Management/Details';
import Equipmng from '@/view/Management/Equipmng';
import Rolemng from '@/view/Management/Rolemng';
import Skillmng from '@/view/Management/Skillmng';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/Customer',
      name: 'Customer',
      component: Customer,
      children: [{
        path: '/Customer/Login',
        name: 'login',
        component: Login
      },
      {
        path: '/Customer/Register',
        name: 'register',
        component: Register
      }]
    },
    {
      path: '/Application',
      name: 'app',
      component: Application
    },
    {
      path: '/Menu',
      name: 'menu',
      component: Menu,
      children: [{
        path: '/Menu/Chapter',
        name: 'chapter',
        component: Chapter
      },
      {
        path: '/Menu/Novel',
        name: 'novel',
        component: Novel
      },
      {
        path: '/Menu/Role',
        name: 'role',
        component: Role
      },
      {
        path: '/Menu/Volume',
        name: 'volume',
        component: Volume
      },
      {
        path: '/Menu/Menulist/',
        name: 'menulist',
        component: Menulist
      },
      {
        path: '/Menu',
        redirect: '/Menu/Menulist'
      }]
    }, {
      path: '/Management',
      name: 'management',
      component: Management,
      children: [{
        path: '/Management/Bookslist',
        name: 'bookslist',
        component: Bookslist
      },
      {
        path: '/Management/Details/:bookid',
        name: 'details',
        component: Details
      },
      {
        path: '/Management/Equipmng',
        name: 'equipmng',
        component: Equipmng
      },
      {
        path: '/Management/Rolemng',
        name: 'rolemng',
        component: Rolemng
      },
      {
        path: '/Management/Skillmng',
        name: 'skillmng',
        component: Skillmng
      },
      {
        path: '/Management',
        redirect: '/Management/Bookslist'
      }]
    },
    {
      path: '/Usercenter',
      name: 'usercenter',
      component: Usercenter
    },
    {
      path: '/',
      redirect: '/Customer/Login'
    }]
});



