<style>
.lication-main{
  position: relative;
  padding-bottom: 2rem;
  height: 100%;
  max-width: 640px;
  margin: 0 auto;
  overflow-y: scroll;
}
.banner{
  height: 4.8rem;
  z-index: 0;
  overflow: hidden;
}
.banner img{
  width: 100%;
  margin-top: -2px;
}
.bookrack{
  min-height: 11rem;
  z-index: 1;
}
.bookrack::after{
  content: "";
  clear: both;
  display:block;
}
.booklist{
  width:100%;
  padding: 0 .4rem;
}
.booklist li{
  padding: .4rem 0;
  border-bottom:1px solid #eee;
}
.booklist li a{
  display: block;
}
.booklist li a::after{
  content: "";
  clear: both;
  display:block;
}
.bookcover{
  position: relative;
  height: 2.1333rem;
  width: 1.6rem;
  float: left;
}
.bookcover img{
  height: 100%;
  width: 100%;
}
.bookcover::after{
  content: '';
  display: block;
  position: absolute;
  height: 1px;
  width: 80%;
  bottom: 0;
  left: 10%;
  box-shadow: 0 .1rem .25rem #000;
}
.bookinfos{
  float: left;
  height: 100%;
  margin-left:.5rem ;
}
.bookinfos div{
  height: 1.0666rem;
  line-height: 1.0666rem;
  text-align: left;
}
.bookinfos .name{
  font-size: .48rem;
  font-weight: bold;
  color: #333;
}
.bookinfos .new-chapter{
  font-size: .4rem;
  color: #888;
}
.lication-main .header i{font-size: .48rem!important;}
</style>
<template>
  <div class="lication-main" @scroll="scl()" ref="main">
    <child :rgba="rgba">
      <span slot="center-tit">主页</span>
      <div class="link-right" slot="right-icon">
        <router-link to="/Usercenter">
          <i class="iconfont" style="color:#fff">&#xe603;</i>
        </router-link>
      </div>
    </child>
    <div class="banner">
      <img src="/static/images/banner.png" alt="">
    </div>
    <div class="bookrack">
      <ul class="booklist">
        <li v-for="(book, index) in books" :key="index">
          <router-link :to="{ name: 'details', params: { bookid: book.id }}" :book="books[index]">
            <div class="bookcover">
              <img src="/static/images/defaultcover.png" alt="">
            </div>
            <div class="bookinfos">
              <div class="name">{{book.name}}</div>
              <div class="new-chapter">已更新到 {{book.nestchapter}} 章</div>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
    <Footer />
  </div>
</template>
<script>
import Header from '@/components/Header';
import Footer from '@/components/Footer';
export default {
  props: {
    books: Array
  },
  data () {
    return {
      opacity: 0,
      act: 0
    };
  },
  computed: {
    rgba () {
      return 'rgba(64,158,255,' + this.opacity + ')';
    }
  },
  mounted () {
    this.act = parseInt(this.$route.params.act);
  },
  components: {
    Footer,
    'child': Header
  },
  methods: {
    scl () {
      let scrollval = this.$refs.main.scrollTop;
      this.opacity = (scrollval > 100 ? 100 : scrollval) / 100;
    }
  }
};
</script>



